






























































.region-picker
  font-size 14px
  position relative
  display inline-block
  width 100%
  .picker-toggle
    position relative

    .picker-label
      position absolute
      left 0
      top 0
      height 100%
      line-height 36px
      padding 0 25px 0 10px
      color #1f2d3d
      width 100%
      white-space nowrap
      text-overflow ellipsis
      overflow hidden
      box-sizing border-box
      cursor pointer
      font-size 14px
      text-align left

    input
      background-color #fff
      position relative
      overflow hidden
      text-overflow ellipsis
      cursor pointer
      border-radius 4px
      border 1px solid #c0ccda
      color #1f2d3d
      font-size inherit
      height 36px
      box-sizing border-box
      line-height 1
      padding 3px 15px 3px 10px
      width 100%
      &:hover
        border-color #8391a5
      &:focus
        outline none
        border-color #20a0ff

    .picker-input-icon
      position absolute
      top 11px
      right 10px
      color #E0E6ED
      cursor pointer
      &.el-icon-circle-close:hover
        color #8391a5
    &.opened.picker-input-icon
      border-bottom-color #E0E6ED

  .picker-menu
    position absolute
    max-height 400px
    z-index 1000
    position absolute
    margin-top 5px
    background-color #ffffff
    border 1px solid #E0E6ED
    border-radius 2px
    box-shadow 0px 2px 4px 0px rgba(0,0,0,0.12), 0px 0px 6px 0px rgba(0,0,0,0.04)
    &:hover
      border-color #20a0ff
    .scroll-search
      min-width 350px
      .scroll-option
        li
          &:hover
            background-color #eff2f7
    .scroll-options
      display flex
      margin-left 1px
    .scroll-option
      min-width 150px
      max-height 275px
      overflow-y scroll
      padding 0
      margin 0
      border-right 1px solid #E0E6ED
      list-style none

      li
        line-height 36px
        cursor pointer
        position relative
        padding 0 10px
        white-space nowrap
        &.hover
          background-color #eff2f7



